import React from 'react';
import './style/App.css';
import ListItems from './ListItems'


class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      items:[],
      currentItem:{
        text:'',
        key:''
      }
    }
  }

  handleInput = (e) => {
    this.setState({
      currentItem:{
        text: e.target.value,
        key: Date.now()
      }
    })
  }

  addItem = (e) => {
    e.preventDefault();
    const newItem = this.state.currentItem;
    if(newItem.text !==""){
      const items = [...this.state.items, newItem];
      this.setState({
        items: items,
        currentItem:{
          text:'',
          key:''
        }
      })
    }
  }

  deleteItem = (key) => {
    const filteredItems= this.state.items.filter(item =>
      item.key!==key);
    this.setState({
      items: filteredItems
    })
  }

  setUpdate = (text,key) => {
    const items = this.state.items;
    items.map(item=>{
      if(item.key===key){
        item.text= text;
      }
    })
    this.setState({
      items: items
    })
  }

  render(){
    return (
      <div className="app">
      <header>
        <h1>Zapisz swoje zadania!</h1>
        <div className="headContainer">
        <form className="form" onSubmit={this.addItem}>
          <input type="text" placeholder="Wpisz zadanie"
          value={this.state.currentItem.text} onChange={this.handleInput}/>
          <button type="submit">Dodaj</button>
        </form>
        </div>
        <h1 className="listText">Do zrobienia:</h1>
        <p>{this.state.items.text}</p>
         <ListItems items={this.state.items} deleteItem={this.deleteItem} setUpdate={this.setUpdate}/>
      </header>
      </div> 
  );
}
}

export default App;
